<template>
  <div class="container" id="app">
    <h1>Simple <span>VueJS</span> Todos</h1>
    <header>
      <input type="text" v-model="todo" :class="inputCls" autofocus>
      <span class="addBtn">
              <i @click="inputCls='inputbox extend'" class="fa fa-plus" v-if="!showIcon"></i>
              <i @click="addTodos" class="fa fa-check" v-else></i>
          </span>
    </header>
    <section>
      <div class="todos" v-if="todos.length > 0">
        <transition-group name="list" tag="ul" class="list-ul">
          <li v-for="(item, index) in todos" :key="item.task">
            <span :class="{complete: item.completed}">{{ item.task }}</span>
            <span>
                  <i @click="undoComplete(index)" class="fa fa-undo" v-if="item.completed"></i>
                  <i @click="undoComplete(index)" class="fa fa-check" v-else></i>
                  <i @click="deleteTodo(index)" class="fa fa-close"></i>
                </span>
          </li>
        </transition-group>
      </div>
      <h3 v-else>No tasks to display. Please add one.</h3>
    </section>
  </div>
</template>

<script>

  const storage = require('../static/storage');
  
  export default {
    name: 'app',
    data() {
      return {
        todo: '',
        todos: [],
        defaultTodo: [{
          task: 'First task',
          completed: false
        }],
        showIcon: false,
        inputCls: 'inputbox'
      }
    },
  
    watch: {
      todo(value) {
        if (value.length > 2) {
          this.showIcon = true;
        } else {
          this.showIcon = false;
        }
      }
    },
  
    methods: {
      addTodos() {
        this.inputCls = 'inputbox';
        this.todos.unshift({
          task: this.todo,
          completed: false
        });
        this.todo = '';
        setTimeout(() => {
          this.showIcon = false;
        }, 1000);
      },
      undoComplete(index) {
        this.todos[index].completed = !this.todos[index].completed;
      },
      deleteTodo(index) {
        this.todos.splice(index, 1);
      }
    },
  
    created() {
      if(storage.checkStorage()){
        this.todos = storage.getTodos();
      } else {
        this.todos = this.defaultTodo;
      }
    },
  
    updated() {
      storage.saveTodos(this.todos);
    }
  }
</script>

<style>
  @import url('https://fonts.googleapis.com/css?family=Oswald');
  
  * {
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Oswald', sans-serif;
  }
  
  a {
    text-decoration: none;
  }
  
  i {
    cursor: pointer;
  }
  
  header {
    padding: 1em;
    position: relative;
  }

  section {
    padding: 1.5em 1em;
  }
  
  .container {
    max-width: 500px;
    width: 100%;
    margin: 40px auto;
  }
  
  .container h1 {
    font-size: 2em;
    text-align: center;
    text-transform: uppercase;
    padding: 1em;
  }
  .container h1 span {
    color: #0CC92B;
  }
  
  header input {
    flex: 1;
    padding: 0 10px;
    width: 95%;
    height: 30px;
    outline: none;
    border-radius: 5px;
    border: 1px solid #C6C6C6;
  }
  
  header .addBtn {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    top: 50%;
    right: 15px;
    z-index: 10;
    transform: translateY(-50%);
  }
  
  header .addBtn i {
    display: block;
    flex: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    color: #FFF;
    font-size: 1.5em;
    background-color: #0CC92B;
  }
  
  .inputbox {
    transform: scaleX(0);
    transform-origin: right center;
    transition: all .4s ease-out;
    opacity: 0;
  }
  
  .extend {
    transform: scaleX(1);
    opacity: 1;
  }
  
  .todos li {
    margin: 0 0 10px 0;
    padding: 10px;
    border-bottom: 1px dotted #C6C6C6;
    display: flex;
    justify-content: space-between;
  }
  
  .todos .fa-undo {
    color: #0CC92B;
  }
  
  .todos .fa-check {
    color: #0CC92B;
  }
  
  .todos .fa-close {
    color: #E20A16;
  }
  
  .complete {
    text-decoration: line-through;
    color: #E20A16;
  }
  
  .list-ul {
    transition: all .7s;
  }
  
  /* Add task transition */
  
  .list-enter-active,
  .list-leave-active {
    transition: opacity .7s, transform .7s;
  }
  
  .list-enter,
  .list-leave-to
  /* .list-leave-active below version 2.1.8 */
  
  {
    opacity: 0;
    transform: translateY(-10px);
  }
</style>



